.tg-turnkey {
  &__section-1 {
    &__block {
      width: 100%;
      &__content {
        position: relative;
        width: calc(100% - 45px);
        min-height: 285px;
        background: #fff;
        margin-bottom: 140px;
        box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.35);
        @media (max-width: 768px) {
          padding-bottom: 0;
          margin-bottom: 90px;
        }
        .part-of-border {
          position: absolute;
          width: 270px;
          height: 22px;
          top: -10px;
          left: -18px;
        }
        .lower-block {
          position: absolute;
          width: 488px;
          right: -20px;
          @media (max-width: 768px) {
            width: auto;
            top: 30px;
            right: -30px;
            position: relative;
          }
          ul {
            @media (max-width: 768px) {
              margin-bottom: 0;
              padding-right: 5px;
            }
          }
          li {
            color: #fff;
            list-style-position: inside;
          }
        }
      }
    }
  }
  &__section-2 {
    background-image: url('../img/mask-6.png');
    width: 100%;
    height: 500px;
  }
}